import React, { Component } from 'react';
import {Polyline, GoogleApiWrapper, Marker} from 'google-maps-react';
import BumpsMap from './bumps-map';
import FavoritiesMap from './favorities-map';
import Car from './car';
import * as helpers from '../../helpers/index';
import '../../styles/report-bumps.css';

class Main extends Component {
	render() {
		let { bumps, favorities, selectedTrip, dimentions, google } = this.props;
		let tripMap, trips = <div>No data</div>;
		let bumpsMap = bumps.map(bump => (<Marker position={{lat: bump.lat, lng: bump.lng}} />));


		if (favorities.length) { // вынести в компонент
			trips = favorities.map((trip, i) => (<div onClick={()=> this.props.selectTrip(i)} key={i} className="temp-button">{i}</div>));
		} 		

		if (favorities.length && selectedTrip !== null) tripMap = <FavoritiesMap google={google} favorities={favorities[selectedTrip]} dimentions={dimentions} />
		
			if (!this.props.isFavoritiesView) {
				return (
					<div 
					className="map">
					<BumpsMap 
						centerAroundCurrentLocation
						google={this.props.google}
						updateLocationData={this.props.updateLocationData}
						currentLocation={this.props.currentLocation}
						isRecordingMode={this.props.isRecordingMode}
						onZoomChanged={this.props.onZoomChanged}		
						dimentions={dimentions}
						camelize={helpers.camelize}						
						>
						<Car 
							angle={this.props.angle}
							move={this.props.move}
							resetMove={this.props.resetMove}
						/>
						<Polyline
							path={this.props.data}
							strokeColor="#4D8FAC"
							strokeOpacity={0.8}
							strokeWeight={4} 
						/>
						{bumpsMap}
					</BumpsMap>	
					</div>
				);
			} else {
				return (
				<div 
					className="map favorities-map" 
					style={{display: this.props.isFavoritiesView ? "flex" : "none"}}>						
						<div className="favorities-list">{trips}</div>
						{tripMap}								
				</div>	
				);
			}				
	}
}

export default Main;